import React, { Component } from 'react';
import './Form.css'

class Form extends Component {
    constructor() {
        super();
        this.state = {
            q1: "",
            a1: "",
            q2: "",
            a2: "",
            q3: "",
            a3: "",
            score:0,
        }
    }

    check1(event) {
        if (event.target.value === "Coastal redwood") {
            this.setState({
                a1: "correct answer",
                score:this.state.score +  1 ,
            })
        }
        else {
            this.setState({
                a1: "wrong answer",
            })
        }
    }

    check2(event) {
        if (event.target.value === "China") {
            this.setState({
                a2: "correct answer",
                score:this.state.score +  1 ,
            })
        }
        else {
            this.setState({
                a2: "wrong answer",
            })
        }
    }



    check3(event) {
        if (event.target.value === "Juniper") {
            this.setState({
                a3: "correct answer",
                score:this.state.score +  1 ,
            })
        }
        else {
            this.setState({
                a3: "wrong answer",
            })
        }
    }

    submitForm(event) {
        event.preventDefault();
    }

    render() {
        return (
            <div className="Form">
                <h1>QUIZ</h1>
                <h2>SCORE : {this.state.score}</h2>
                
                <form onSubmit={this.submitForm.bind(this)}>
                    <label htmlFor="">1.Which Countries Are Planting the Most Trees??</label><br /><br />
                    <label htmlFor="">China</label>
                    <input type="radio" name="fast" id="" onChange={this.check2.bind(this)} value="China" checked={this.state.q2 === "China"} />
                    <label htmlFor="">USA</label>
                    <input type="radio" name="fast" id="" onChange={this.check2.bind(this)} value="USA" checked={this.state.q2 === "USA"} /><br /><br />
                    <label htmlFor="">Australia</label>
                    <input type="radio" name="fast" id="" onChange={this.check2.bind(this)} value="Australia" checked={this.state.q2 === "Australia"} />
                    <label htmlFor="">Pakistan</label>
                    <input type="radio" name="fast" id="" onChange={this.check2.bind(this)} value="Pakistan" checked={this.state.q2 === "Pakistan"} /><br /><br />


                    <label htmlFor="">2.What is the tallest tree in the world??</label><br /><br />
                    <label htmlFor="">English oak (Quercus robur)</label>
                    <input type="radio" name="fast" id="" onChange={this.check1.bind(this)} value="English oak (Quercus robur)" checked={this.state.q1 === "English oak (Quercus robur)"} />
                    <label htmlFor="">. Sugar pine (Pinus lambertiana)</label>
                    <input type="radio" name="fast" id="" onChange={this.check1.bind(this)} value="Sugar pine (Pinus lambertiana)" checked={this.state.q1 === "Sugar pine (Pinus lambertiana)"} /><br /><br />
                    <label htmlFor="">Douglas fir (Pseudotsuga menziesii)</label>
                    <input type="radio" name="fast" id="" onChange={this.check1.bind(this)} value="Douglas fir (Pseudotsuga menziesii)" checked={this.state.q1 === "Douglas fir (Pseudotsuga menziesii)"} />
                    <label htmlFor="">Coastal redwood (Sequoia sempervirens)</label>
                    <input type="radio" name="fast" id="" onChange={this.check1.bind(this)} value="Coastal redwood" checked={this.state.q1 === "Coastal redwood"} /><br /><br />



                    <label htmlFor="">3. Which of the following trees does NOT produce nuts?</label><br /><br />
                    <label htmlFor="">Hazel</label>
                    <input type="radio" name="fast" id="" onChange={this.check3.bind(this)} value="Hazel" checked={this.state.q3 === "Hazel"} />
                    <label htmlFor="">Hickory</label>
                    <input type="radio" name="fast" id="" onChange={this.check3.bind(this)} value="Hickory" checked={this.state.q3 === "Hickory"} /><br /><br />
                    <label htmlFor="">Juniper</label>
                    <input type="radio" name="fast" id="" onChange={this.check3.bind(this)} value="Juniper" checked={this.state.q3 === "Juniper"} />
                    <label htmlFor="">Almond</label>
                    <input type="radio" name="fast" id="" onChange={this.check3.bind(this)} value="Almond" checked={this.state.q3 === "Almond"} /><br /><br />
                   
                    <h2>Result</h2>
                    <h3>{this.state.a2}</h3>
                    <h3>{this.state.a1}</h3>
                    <h3>{this.state.a3}</h3>
                </form>
            </div>
        )
    }
}

export default Form;